body, html {
  height: 100%;
  font-family: Arial, sans-serif;
}

body {
  box-sizing: border-box;
  background-color: #1a1a1a;
  background-image: url("backgroundMap.533f521b.png");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  justify-content: center;
  align-items: center;
  min-height: 100vh;
  margin: 0;
  padding: 1rem;
  display: flex;
}

.game-container {
  aspect-ratio: 16 / 9;
  background-color: #0f0c27b6;
  border: 2px solid #555;
  border-radius: 15px;
  width: 100%;
  max-width: 1400px;
  max-height: 95vh;
  position: relative;
  overflow: hidden;
  box-shadow: 0 0 75px #fff;
}

.game-main-title {
  color: #fff;
  text-align: center;
  z-index: 10;
  text-shadow: 2px 2px 4px #00000080;
  margin: 0;
  padding: 0;
  font-size: clamp(1.5em, 4vw, 2.5em);
  position: absolute;
  top: 20px;
  left: 50%;
  transform: translateX(-50%);
}

.screen {
  box-sizing: border-box;
  width: 100%;
  height: 100%;
  padding: 20px;
  display: none;
  position: absolute;
  top: 0;
  left: 0;
}

.screen.active {
  flex-direction: column;
  justify-content: center;
  align-items: center;
  display: flex;
}

#titleScreen img#gameTitleImage {
  object-fit: contain;
  max-width: 70%;
  max-height: 50%;
  margin-bottom: 30px;
}

#titleScreen button#startButton, #characterSelectionScreen .character-options button {
  cursor: pointer;
  color: #fff;
  background-color: red;
  border: none;
  border-radius: 8px;
  padding: clamp(10px, 2vh, 15px) clamp(20px, 4vw, 30px);
  font-size: clamp(1em, 2.5vw, 1.5em);
  transition: background-color .3s, transform .1s;
  box-shadow: 0 4px 6px #0000001a;
}

#titleScreen button#startButton:hover, #characterSelectionScreen .character-options button:hover {
  color: #000;
  background-color: gold;
}

#titleScreen button#startButton:active, #characterSelectionScreen .character-options button:active {
  transform: translateY(1px);
}

#characterSelectionScreen #confirmCharacterButton {
  color: #fff;
  cursor: pointer;
  background-color: #28a745;
  border: none;
  border-radius: 5px;
  padding: 12px 25px;
  font-size: 1.1em;
  position: absolute;
  bottom: 30px;
  right: 40px;
  box-shadow: 0 2px 4px #0003;
}

#characterSelectionScreen.active {
  justify-content: flex-start;
  align-items: center;
  padding-top: 40px;
}

#characterSelectionScreen h2 {
  color: #fff;
  text-align: center;
  width: 100%;
  margin-bottom: clamp(20px, 5vh, 50px);
  font-size: clamp(1.2em, 3vw, 2em);
}

#characterSelectionScreen .character-selection-content {
  flex-direction: row;
  align-items: flex-start;
  gap: clamp(20px, 10vw, 175px);
  width: 95%;
  max-width: 1100px;
  display: flex;
}

#characterSelectionScreen .character-options {
  flex-direction: column;
  flex-shrink: 0;
  gap: 50px;
  display: flex;
}

#characterPortraits {
  flex-grow: 1;
  grid-template-rows: repeat(2, auto);
  grid-template-columns: repeat(3, 1fr);
  gap: clamp(15px, 5vw, 75px);
  min-width: 0;
  max-width: 700px;
  display: grid;
}

#characterPortraits img {
  aspect-ratio: 4 / 5;
  object-fit: contain;
  background-color: #0000;
  border: clamp(3px, .5vw, 6px) solid gold;
  border-radius: 6px;
  width: 100%;
}

#characterPortraits img.selected {
  border-color: #0f0;
  box-shadow: 0 0 15px #0f0;
}

#gameplayScreen.active {
  flex-direction: row;
  justify-content: space-between;
  padding: 10px;
  display: flex;
}

.tooltip {
  color: #fff;
  z-index: 1000;
  pointer-events: none;
  background-color: #000000d9;
  border: 1px solid red;
  border-radius: 6px;
  padding: 10px 15px;
  font-size: .9em;
  line-height: 1.6;
  display: none;
  position: absolute;
}

.gameplay-left-panel {
  background-color: #0a0a1eb3;
  border-radius: 8px;
  flex-direction: column;
  flex-grow: 1;
  height: 600px;
  margin-top: 55px;
  margin-right: 10px;
  padding: 10px;
  display: flex;
  overflow: hidden;
}

.text-area {
  flex-grow: 1;
  min-height: 0;
  margin-bottom: 10px;
  display: flex;
}

.output-text-container {
  color: #e0e0e0;
  background-color: #0009;
  border: 1px solid #444;
  border-radius: 5px;
  flex-grow: 1;
  padding: 15px;
  overflow-y: auto;
}

.output-text p {
  word-wrap: break-word;
  margin: 0 0 .75em;
  line-height: 1.5;
}

.input-area input[type="text"] {
  color: #fff;
  background-color: #222;
  border: 1px solid #555;
  border-radius: 5px;
  width: calc(100% - 22px);
  padding: 10px;
  font-size: 1em;
}

.gameplay-center-panel {
  background-color: #0f0f23b3;
  border-radius: 8px;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 350px;
  height: 400px;
  margin-left: 75px;
  margin-right: 75px;
  padding: 10px;
  display: flex;
}

.monster-display-area {
  color: #777;
  text-align: center;
  border: 1px dashed #555;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 400px;
  display: flex;
}

.character-display-box {
  aspect-ratio: 4 / 5;
  background-color: #00000080;
  border: 4px solid gold;
  border-radius: 6px;
  justify-content: center;
  align-items: center;
  width: calc(100% - 8px);
  margin-bottom: 15px;
  display: flex;
  overflow: hidden;
}

.character-display-box img {
  object-fit: contain;
  max-width: 100%;
  max-height: 100%;
}

.side-panel {
  background-color: #141428cc;
  border-radius: 8px;
  flex-direction: column;
  flex-shrink: 0;
  gap: 15px;
  width: 220px;
  padding: 15px;
  display: flex;
}

.side-panel .panel-item {
  color: #c0c0ff;
  cursor: pointer;
  background-color: #282846e6;
  border-radius: 5px;
  align-items: center;
  padding: 10px;
  transition: background-color .2s;
  display: flex;
}

.side-panel .panel-item:hover {
  background-color: #3c3c64e6;
}

.side-panel .panel-icon {
  margin-right: 10px;
  font-size: 1.2em;
}

.stat-bar-container {
  width: 100%;
  margin-bottom: 10px;
}

.stat-bar-label {
  color: #ccc;
  text-align: left;
  margin-bottom: 3px;
  font-size: .8em;
}

.stat-bar-background {
  background-color: #333;
  border: 1px solid #555;
  border-radius: 5px;
  width: 100%;
  height: 18px;
  overflow: hidden;
}

.stat-bar-fill {
  background-color: green;
  border-radius: 4px;
  width: 0%;
  height: 100%;
  transition: width .3s ease-in-out;
}

#experienceBarFill {
  background-color: #007bff;
}

.stat-bar-text {
  color: #fff;
  text-align: center;
  pointer-events: none;
  margin-top: -17px;
  font-size: .75em;
  position: relative;
}
